//defaults
*,
*:before,
*:after {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box; 
}
html,body,
div,span,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,abbr,address,cite,code,del,dfn,em,img,ins,kbd,q,samp,small,strong,sub,sup,var,b,i,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,figcaption,figure,footer,header,hgroup,menu,nav,section,summary,
time,mark,audio,video{
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	vertical-align: baseline;
	outline: 0;
	background: transparent;
}
body{
	line-height: 1.4;
}
article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{
	display: block;
}
nav ul,
.nav ul{
	list-style: none;
}
li{
	
}

blockquote,
q{
	quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after{
	content: '';
	content: none;
}
a{
	margin: 0;
	padding: 0;
	font-size: 100%;
	vertical-align: baseline;
	background: transparent;
}
ins{
	text-decoration: none;
	color: #000;
	background-color: #ff9;
}
mark{
	font-style: italic;
	font-weight: bold;
	color: #000;
	background-color: #ff9;
}
del{
	text-decoration: line-through;
}
abbr[title],
dfn[title]{
	border-bottom: 1px dotted;
	cursor: help;
}
table{
	border-collapse: collapse;
	border-spacing: 0;
}
hr{
	display: block;
	height: 1px;
	margin: 1em 0;
	padding: 0;
	border: 0;
	border-top: 1px solid #cccccc;
}
input,
select{
	vertical-align: middle;
}
a{
	text-decoration: none;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;

}
a:hover {
	cursor: pointer; 
}
a:active { 
	outline:0 !important;
	-webkit-tap-highlight-color: rgba(0,0,0,0);
}
img {
	-ms-interpolation-mode: bicubic;
	max-width: 100%;
	height: auto; 
}